<template>
	<view class="container">

		<!-- 原有的内容 -->
		<view class="question-item">
			<view class="question-title">1：您的预算</view>
			<view class="select-wrap">
				<picker @change="budgetChange" :value="budgetIndex" :range="budgetOptions" range-key="text">
					<view class="picker-content">
						{{ formData.budget ? budgetOptions[budgetIndex].text : '请选择预算范围' }}
					</view>
				</picker>
			</view>
		</view>

		<view class="question-item">
			<view class="question-title">2：您所在地区</view>
			<view class="input-wrap">
				<input type="text" v-model="formData.area" placeholder="请输入您所在的地区" />
			</view>
		</view>

		<view class="submit-wrap">
			<button @tap="submitForm" class="submit-btn">提交</button>
		</view>
	</view>
</template>

<script>
const util = require("@/utils/util.js")
import {
		setUserInfo
	} from '@/utils/auth';
export default {
	data() {
		return {
			formData: {
				budget: '',
				area: ''
			},
			budgetIndex: 0,
			budgetOptions: [{
				text: '10万以下',
				value: '10万以下'
			}, {
				text: '10-50万',
				value: '10-50万'
			}, {
				text: '50万以上',
				value: '50万以上'
			}]
		}
	},
	methods: {
		budgetChange(e) {
			this.budgetIndex = e.detail.value;
			this.formData.budget = this.budgetOptions[this.budgetIndex].value;
		},
		submitForm() {
			if (!this.formData.budget) {
				uni.showToast({
					title: '请选择预算',
					icon: 'none'
				});
				return;
			}
			if (!this.formData.area) {
				uni.showToast({
					title: '请输入所在地区',
					icon: 'none'
				});
				return;
			}

			console.log('表单数据：', this.formData);

			uni.showLoading({
				title: '提交中',
				mask: true
			})
			util.request("wx/Question/saveQuestion", this.formData, "POST").then(function (res) {
				uni.hideLoading();
				if (res.code === 200) {
					util.request("wxGetInfo", "", "GET").then(function (wxGetInfoRes) {
						setUserInfo(wxGetInfoRes.user)
						if (wxGetInfoRes.user.customerType == 4) {
							util.toast("请进入小程序员工端!")
						}
						if (!wxGetInfoRes.user.phonenumber) {
							uni.reLaunch({
								url: '/pages/me/userinfo',
							})
						} else {
							//customerType  没有提交问卷=0;  提交问卷没有预约=1;  预约了=2;  签约了的=3 不是客户 = 4
							if (wxGetInfoRes.user.customerType == 0) {
								uni.reLaunch({
									url: '/pages/question/index',
								})
							} else if (wxGetInfoRes.user.customerType == 1 || wxGetInfoRes.user.customerType == 2) {
								uni.reLaunch({
									url: '/pages/prebook/index',
								})
							} else  {
								uni.switchTab({
									url: '/pages/index/index',
								})
							}
						}
					})
				} else {
					uni.showToast({
						icon: "none",
						title: res.msg,
						duration: 2000
					})
				}
			})
		}
	}
}
</script>

<style>
.container {
	padding: 30rpx;
	background: #fff;
}

.question-item {
	margin-bottom: 40rpx;
}

.question-title {
	font-size: 28rpx;
	color: #333;
	margin-bottom: 20rpx;
}

.select-wrap {
	padding: 0 20rpx;
}

.picker-content {
	height: 80rpx;
	line-height: 80rpx;
	background: #f8f8f8;
	border-radius: 8rpx;
	padding: 0 20rpx;
	font-size: 28rpx;
	color: #333;
}

.input-wrap {
	padding: 20rpx;
}

.input-wrap input {
	width: 100%;
	height: 80rpx;
	background: #f8f8f8;
	border-radius: 8rpx;
	padding: 0 20rpx;
	font-size: 28rpx;
}

.submit-wrap {
	margin-top: 60rpx;
	padding: 0 40rpx;
}

.submit-btn {
	width: 100%;
	height: 88rpx;
	line-height: 88rpx;
	background: #2979FF;
	color: #fff;
	font-size: 32rpx;
	border-radius: 8rpx;
}
</style>